.simplebar-track.simplebar-vertical {
    width: 15px;
    // background: #f8f8f8;
}

.simplebar-track.simplebar-horizontal {
    height: 15px;
    // background: #f8f8f8;
}

.simplebar-scrollbar:before {
    border-radius: 1rem;
    background: #204a8f;
}

.simplebar-scrollbar.simplebar-visible::before {
    opacity: 1;
}

.simplebar-scrollable-x {
}

@media (max-width: 599.98px) {
    .scroll-area {
        &--full {
            .simplebar-track.simplebar-horizontal {
                left: 20px;
                right: 20px;
            }
        }
    }
}

.mfp-move-horizontal {
    /* start state */
    .mfp-with-anim {
        opacity: 0;
        transition: all 0.3s;

        transform: translateX(-50px);
    }

    &.mfp-bg {
        opacity: 0;
        transition: all 0.3s;
    }

    /* animate in */
    &.mfp-ready {
        .mfp-with-anim {
            opacity: 1;
            transform: translateX(0);
        }
        &.mfp-bg {
            opacity: 0.8;
        }
    }

    /* animate out */
    &.mfp-removing {
        .mfp-with-anim {
            transform: translateX(50px);
            opacity: 0;
        }
        &.mfp-bg {
            opacity: 0;
        }
    }
}

.slider-titles {
    .swiper-pagination {
        width: auto !important;
        bottom: 5rem !important;
        left: 6rem !important;
    }
    .swiper-pagination-bullet {
        width: 1.2rem;
        height: 1.2rem;
        margin: 0 0.9rem !important;
        background: rgba(217, 217, 217, 0.5);
    }

    .swiper-pagination-bullet-active {
        background: #d9d9d9;
    }
}
